/* src/components/Skills/Skills.css */

.skills-section {
  padding: 80px 0;
  background: #f8f9fa;
  position: relative;
}

.skills-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  z-index: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.container h2 {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  color: #212529;
  margin-bottom: 40px;
  font-family: 'Inter', sans-serif;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 50px;
}

.skill-category {
  background: #ffffff;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid #e9ecef;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.skill-category h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #212529;
  margin-bottom: 20px;
  font-family: 'Inter', sans-serif;
  display: flex;
  align-items: center;
  gap: 12px;
}

.skill-category h3 i {
  color: #007bff;
  font-size: 1.8rem;
}

.skills-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skills-list li {
  padding: 12px 0;
  border-bottom: 1px solid #f1f3f4;
  color: #495057;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

.skills-list li:last-child {
  border-bottom: none;
}

.skills-list li i {
  color: #007bff;
  font-size: 0.9rem;
  width: 16px;
}

.skill-level {
  margin-left: auto;
  font-size: 0.85rem;
  color: #6c757d;
  font-weight: 500;
}

/* Responsive design */
@media (max-width: 768px) {
  .skills-section {
    padding: 60px 0;
  }
  
  .container h2 {
    font-size: 2rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .skill-category {
    padding: 25px;
  }
  
  .skill-category h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  .container h2 {
    font-size: 1.8rem;
  }
  
  .skill-category {
    padding: 20px;
  }
  
  .skill-category h3 {
    font-size: 1.2rem;
  }
  
  .skills-list li {
    font-size: 0.95rem;
  }
}